*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    scroll-behavior: smooth;
}

body {
    background-color:#1f2937 ;
    color: #fafaf9;
    font-family: 'Sono', sans-serif;
    padding: 40px;
    
}

.hidden {
    display: none !important;
}

.message {
    text-align: center;
    text-transform: uppercase;
    font-size: 32px;
    font-weight: 700;
    margin-top: 32px;
}

#root {
    max-width: 1500px;
    margin: 0 auto;
}

header {
    margin-bottom: 40px;
    padding: 10px;

    display: flex;
    align-items: center;
    justify-content: space-between;
}

.logo {
    display: flex;
    align-items: center;
    gap: 16px;
}



main {
    display: grid;
    grid-template-columns: 250px 1fr;
    gap: 48px;
}

@media screen and (max-width: 900px) {
    main {
        grid-template-columns: 1fr;
    }
}

.logo img {
    width: 68px;
}

h1 {
    font-size: 42px;
    text-transform: uppercase;
    font-family: 'Coiny', cursive;
    line-height: 1;
    margin-top: 6px;
}

.category {
    margin-bottom: 16px;
}

.fact {
    font-size: 20px;
    line-height: 1.4;
    background-color: #52525b;
    margin-bottom: 16px;
    padding: 16px 24px;
    letter-spacing: -1px;

    display: flex;
    align-items: center;
    gap: 24px;
    border-radius: 20px;
}

.fact > p {
    width: 100%;
}

@media screen and (max-width: 1200px) {
    .fact {
        flex-direction: column;
        align-items: flex-end;
        gap: 12px;
    }
}

.tag {
    text-transform: uppercase;
    font-size: 14px;
    font-family: 'Coiny', cursive;
    padding-top: 3px;
    padding-left: 10px;
    padding-right: 10px;
    border-radius: 100px;
}

.v-buttons {
    margin-left: auto;
    flex-shrink: 0;

    display: flex;
    gap: 8px;
}

.v-buttons button {
    background-color: hsl(240, 5%, 55%);
    border: none;
    font-size: 18px;
    padding: 6px 12px;
    border-radius: 100px;
    color: inherit;
    font-family: inherit;
    font-weight: 600;
    cursor: pointer;
    transition: .3s;
}

.v-buttons button:hover {
    background-color: hsl(230, 5%, 40%);
}
.v-buttons button:disabled {
    background-color: #52525b;
}


ul {
    list-style: none;
}

.source:visited,
.source:link {
    color: #a8a29e;
    text-decoration: none;
    margin-left: 12px;
    transition: 0.3s;
}

.source:active, 
.source:hover {
    color: #8ebdff;
}

.btn {
    border: none;
    font-family: 'coiny',sans-serif;
    line-height: 1;
    text-transform: uppercase;
    font-size: 17px;
    padding: 16px 0 13px;
    background-image: linear-gradient(135deg, #8ebdff, #ef4444,#16a34a, #eab308);
    color: inherit;
    border-radius: 100px;
    cursor: pointer;
    transition: 0.3s;
}
.btn:hover {
    transform: scale(110%) rotate(-2deg);
}

.btn-large {
    font-size: 20px;
    padding: 20px 32px 17px;
}

.btn-all {
    margin-bottom: 16px;
    width: 100%;
}

.btn-category {
    width: 100%;
    background-image: none;
}

.fact-form {
    background-color: #52525b;
    margin-bottom: 40px;
    padding: 16px 32px;
    display: flex;
    align-items: center;
    gap: 16px;
    border-radius: 20px;
}



.fact-form input, .fact-form select {
    width: 220px;
    background-color: hsl(240, 5%, 55%);
    border: none;
    font-size: 18px;
    padding: 16px;
    border-radius: 100px;
    color: inherit;
    font-family: inherit;
}

.fact-form input::placeholder {
    color:  hsl(240, 5%, 70%) ;
}

.fact-form input:first-child {
    flex-grow: 1;
}

.fact-form span {
    font-weight: 600;
    font-size: 18px;
    margin-right: 18px;
}

@media screen and (max-width: 1000px) {
    body {
        padding: 32px 48px;
    }
    .fact-form {
        flex-direction: column;
        align-items: stretch;
    }
    .fact-form input,
    .fact-form select {
        width: auto;
    }
}